import MasterPage from '../components/Master'
import fetch from 'isomorphic-unfetch'
import Precio from '../components/Precio'
import Noticias from '../components/Noticias'
import Eventos from '../components/Eventos'

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio precio={props.precioBitcoin}/>
            </div>
            <div className="col-md-8">
                <h2>Noticias Sobre Bitcoin</h2>
                <Noticias noticias={props.noticias}/>
            </div>
            <div className="col-md-4">
                <h2>Pr√≥ximos Eventos Bitcoin</h2>
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {
    //const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/') apiDeprecada
    const precio = await fetch('https://pro-api.coinmarketcap.com/v1/cryptocurrency/listings/latest?CMC_PRO_API_KEY=806d7750-2153-4f07-81a8-de4e96424c38')
    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=6c1c1dfcb4a943c7bf481bc628b80153&language=es')
    //const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Mexico&token=4M7VDFZWO3VAGXJ2S7UU')

    const resPrecio = await precio.json();
    const resNoticia = await noticias.json();
    //const resEventos = await eventos.json();
    return {
        precioBitcoin: resPrecio.data[0].quote.USD,
        noticias: resNoticia.articles,
       // eventos: resEventos.events
    }
}

export default Index;